<template>
<div class="find-poi-wrapper container-fluid">
    <div class="row find-tabs">
        <div class="col-6" :class="isFindPassword ? 'active' : ''" @click="changeTab(true)"><span class="find-tab"><span>{{$t("FindPOI.FindId")}}</span></span></div>
        <div class="col-6" :class="isFindPassword ? '' : 'active'" @click="changeTab(false)"><span class="find-tab"><span>{{$t("FindPOI.FindPassword")}}</span></span></div>
    </div>
    <div class="row find-poi-content" v-if="!isFindPassword">
        <TextInput :label="$t('FindPOI.Email')" :wrapperClass="'w-100 find-pass'" :name="'userId'" v-validate="'required|email'" v-model="FormData.userId" />
        <Button primary :label="$t('FindPOI.FindPassword')" :wrapperClass="'find-poi-btn'" @click.native="doFindPw"></Button>
        <div class="find-results success" v-if="isFindPwSuccess && clicked">
            <div class="title">{{$t('FindPOI.Pleasecheckyouremail')}}</div>
            <Button default :label="$t('Login.Login')" :wrapperClass="'add-margin'" v-show="isFindPwSuccess && clicked" @click.native="gotoLogin"></Button>
        </div>
        <div class="find-results error" v-if="!isFindPwSuccess && clicked">
            <div class="title">{{$t('FindPOI.ThePwdnotfound')}}</div>
            <Button default :label="$t('Login.Login')" :wrapperClass="'add-margin invisible'"></Button>
        </div>
    </div>
    <div class="row find-poi-content" v-if="isFindPassword">
        <PhoneInput :label="$t('FindPOI.Phone')" :wrapperClass="'w-100'" :placeholder="''" v-model="FormData.phoneNum" v-validate="'required'" :name="'phoneNum'" :defaultCountry="FormData.phoneNum.regionCode" />
        <Button primary :label="$t('FindPOI.FindId')" :wrapperClass="'find-poi-btn'" @click.native="doFindId"></Button>
        <div class="find-results success" v-if="isFindIdSuccess && clicked">
            <div class="title">{{$t('FindPOI.TheIDyoufoundis')}}</div>
            <div class="response">{{hidden(findIdRes.RST_DATA)}}</div>
            <Button default :label="$t('Login.Login')" :wrapperClass="'add-margin'" v-show="isFindIdSuccess && clicked" @click.native="gotoLogin"></Button>
        </div>
        <div class="find-results error" v-if="!isFindIdSuccess && clicked">
            <div class="title">{{$t('FindPOI.TheIDnotfound')}}</div>
            <div class="response">{{$t('FindPOI.Pleasetryagain')}}</div>
            <Button default :label="$t('Login.Login')" :wrapperClass="'add-margin invisible'"></Button>
        </div>
    </div>
</div>
</template>

<script>
import TextInput from '@/components/Common/Input/TextInput';
import PhoneInput from '@/components/Common/Input/PhoneInput';
import Button from '@/components/Common/Button/Button';
import {
    FIND_ID,
    FIND_PW
} from '@/mutations/Auth.mutation';
import {
    SHOW_POPUP_BUTTON,
    HIDE_POPUP_BUTTON
} from '@/mutations/Common.mutation';
import PhoneCode, {
    findCountry
} from '@/validator/phoneCode';

export default {
    name: 'FindPOI',
    components: {
        TextInput,
        PhoneInput,
        Button
    },
    data: function () {
        return {
            isFindPassword: true,
            clicked: false,
            FormData: {}
        }
    },
    created: function () {
        this.$validator.attach('userId', 'required|email');
        this.$validator.attach('phoneNum', 'required');
        this.FormData['phoneNum'] = {
            country: {
                dialCode: ""
            },
            number: {
                national: '',
                input: ''
            },
        }
        this.FormData['userId'] = null
    },
    methods: {
        changeTab (tab) {
            this.isFindPassword = tab
            this.clicked = false
        },
        doFindId: async function () {
            let isValid = await this.$validator.validate('phoneNum', this.FormData.phoneNum.number.input);
            if (isValid) {
                let data = {
                    userTel: this.FormData.phoneNum.number.input.replace(/ /g, ''),
                    countryNum: this.FormData.phoneNum.country.dialCode
                }
                await this.$store.dispatch(FIND_ID, data);
                this.clicked = true;
            } else {
                let error = this.errors.items[0];
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.msg);
            }
        },
        doFindPw: async function () {
            let isValid = await this.$validator.validate('userId', this.FormData.userId);
            if (isValid) {
                let data = {
                    userId: this.FormData.userId
                }
                await this.$store.dispatch(FIND_PW, data);
                this.clicked = true;
            } else {
                let error = this.errors.items[0];
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.msg);
            }
        },
        gotoLogin: function () {
            return this.$router.push('/auth/login')
        },
        hidden: function (string) {
            let strs = string.split('@');
            if (strs[0].length == 1) {
                strs[0] = '*'
                return strs.join('@');
            }

            let replaceLen = Math.ceil(strs[0].length / 2);
            let replaceStr = '',
                seedStr = '';

            for (let i = 0; i < strs[0].length; i++) {
                if (i >= replaceLen) {
                    replaceStr += '*';
                    seedStr += strs[0][i];
                }
            };
            return strs.join('@').replace(seedStr, replaceStr);
        }
    },
    computed: {
        isFindIdSuccess() {
            let findIdObject = this.$store.state.Auth.findIdObject;
            return findIdObject.isSuccess;
        },
        findIdRes() {
            let findIdObject = this.$store.state.Auth.findIdObject;
            return findIdObject;
        },
        isFindPwSuccess() {
            let findPwObject = this.$store.state.Auth.findPwObject;
            return findPwObject.isSuccess;
        },
        findPwRes() {
            let findPwObject = this.$store.state.Auth.findPwObject;
            return findPwObject;
        },
    }
}
</script>

<style lang="scss">
.find-poi-wrapper {
    input[name="userId"] {
        height: 3.5rem;
    }
}
</style>
